.custom-website-design-banner{
    min-height: fit-content;
    height: calc(100vh - 80px);
    background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692443104/WebsiteDesignAndDevelopment/CustomWebsiteDesign/CustomWebsiteDesignBanner/banner_ni5sq2.webp");
    background-size: 100% 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5vh;
    padding: 0 7vw;
}

.custom-website-design-banner-buttons{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 5%;
    width: 100%;
}

.custom-website-design-banner h1{
    color: #fff;
    font-size: 2.5vw;
    text-align: center;
}

.custom-website-design-banner p{
    color: #fff;
    font-size: 1.75vw;
    text-align: center;
}

.custom-website-design-banner-buttons button{
    padding: 1%;
    font-size: 1.1vw;
    height: fit-content;
    min-width: fit-content;
    width: 12.5%;
    font-weight: bolder;
    background-color: #fff ;
    color: #612D91;
    cursor: pointer;
    border: none;
    transition: all 0.4s ease-in;
}

.custom-website-design-banner-buttons button:hover{
    background-color: #612D91 ;
    color: #fff;
}

@media (max-width: 767px) {
    .custom-website-design-banner{
        height: 30vh;
    }

    .custom-website-design-banner h1{
        font-size: 1rem;
    }

    .custom-website-design-banner p{
        font-size: 0.75rem;
    }

    .custom-website-design-banner-buttons button{
        font-size: 0.6rem;
    }
}